import React, { Component } from 'react'
import {getNowPath} from '../function/someFun'
import {SmallNav} from '../../../components'
import  {getSharePartById} from '../../../services'


const partId = {
    part_id: ''
}

export default class ShowShareItem extends Component {
    constructor(){
        super()
        this.state={
            dataSource:{},
            isLoading:false,
            smNavdata:{
                navData:[],
                nowData:''
            }
        }
    }
    componentDidMount(){
        this.getRoute()
        partId.part_id = this.returnReaId(this.props.match.params.id) 
        partId.part_id =1
        this.getData(partId)
        
        // this.fillAllYearsInfo()
    }

    returnReaId = (idData)=>{
        return idData.substring(1)
    }


    getData = (data)=>{
        getSharePartById(data)
        .then(res =>{
            console.log(res)
           this.setState({
               dataSource: res.data
           })
        })
        .catch(err => {
            console.warn(err)
        })

    }
    getRoute = ()=>{
        const routeArr = getNowPath(this.props.history.location.pathname)
        this.setState({
            smNavdata:{
                navData:routeArr.nav,
                nowData:routeArr.detailRoute,
            }
        })
    }
    render() {
        return (
            <>
                <SmallNav smNavdata={this.state.smNavdata}  />
                <div style={{padding:'50px'}}>
                   <h2>
                       part_id:  {this.state.dataSource.id}
                   </h2>
                    <h4>
                       链接地址： {this.state.dataSource.url}
                    </h4>
                </div>
            </>

        )
    }
}
